import React from "react";
import {GoogleMap, withGoogleMap, withScriptjs,} from "react-google-maps";
import s from "./map.module.css";

const MyGoogleComponent = () =>{

    function Map() {
        return <GoogleMap defaultCenter={{lat: 45, lng: -77}} defaultZoom={14}/>
    }

    const WrappedMap = withScriptjs(withGoogleMap(Map))

    return (
        <div className={s.wrapper}>
            <div className={s.map__item}>
                    <WrappedMap
                        googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=AIzaSyBwv9CN4tSlLq390mrbrHdRbwVrXGYCYsI`}
                        loadingElement={<div style={{height: '100%'}}/>}
                        containerElement={<div style={{height: '100%'}}/>}
                        mapElement={<div style={{height: '100%'}}/>}
                    />
            </div>
        </div>
    )
}

export default MyGoogleComponent;