import React, {useState} from "react";
import {MenuItem, Select} from "@material-ui/core";
import s from "./selectInput.module.css"
import Checkbox from "./Checkbox";
const PhoneInput = ({handleProgressElement, title}) => {
    const [value, setValue] = useState('');

    const formatPhoneNumber = phoneNumberString => {
        const cleaned = ('' + phoneNumberString).replace(/\D/g, '');
        const match = cleaned.match(/^(\d{3})(\d{3})(\d{2})(\d{2})$/);
        if (match) {
            return `(${match[1]}) ${match[2]} - ${match[3]} - ${match[4]}`;
        }
        return null;
    }

    const arrow = <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M2.11707 8.954L9.07108 16.1421L16.0251 8.954" stroke="#C7C7C7" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
    </svg>

    return(
        <div className={`${s.phone} ${s.mt}`}>
            <div className={s.phone__item}>
                <div className={s.form}>
                    <div className={s.circle}/>
                    <Select
                        className={s.select}
                        disableUnderline
                        displayEmpty={true}
                        IconComponent={() =>arrow}
                        value={value}
                        onChange={e => {
                            handleProgressElement(title, true)
                            setValue(e.target.value)
                        }}
                        required={true}
                    >
                        <MenuItem value={'1'}>+7 (000) 000 - 00 - 00</MenuItem>
                        <MenuItem value={'2'}>+7 (000) 222 - 22 - 22</MenuItem>
                        <MenuItem value={'3'}>+7 (000) 111 - 11 - 11</MenuItem>
                    </Select>
                </div>
                <div className={s.checkbox}>
                    <Checkbox text={'Сообщения'}/>
                    <Checkbox text={'Телефон'}/>
                </div>
            </div>
            <a className={s.link} href="#">Подтвердить номер</a>
        </div>
    )
}


export default PhoneInput;