import React, {useState} from "react";
import Checkbox from "./Checkbox";
import s from "./accordionInfo.module.css"

const AccordionInfo = ({ svg, textCheck, text, textInfo}) => {

    const [visible, setVisible] = useState(false);

    return(
    <div className={s.container}>
        <div className={s.accordionInfo}>
            <div className={s.image}>
                <svg className={s.svg} width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    {svg}
                </svg>
                <Checkbox text={textCheck}/>
            </div>
            <div className={s.btn} onClick={() => setVisible(!visible)}>
                <svg
                    className={s.arrow}
                    width="17"
                    height="10"
                    viewBox="0 0 17 10"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    style={visible ? {transform: 'rotate(180deg)'} : {transform: 'rotate(0deg)'}}>
                    <path
                        d="M15.2288 8.18809L8.27475 0.999954L1.32075 8.18809"
                        stroke="#00A0AB"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"/>
                </svg>
                <p className={s.about}>подробнее</p>
            </div>
        </div>
        <div className={s.text__block}>
            <p className={s.info}>
                {text}

            </p>
        </div>
        <div className={s.text__block}>
            <p className={`${s.info} ${s.info__arrow}`} style={visible ? {display : 'block'} : {display: 'none'}}>
                {textInfo}
            </p>
        </div>
    </div>
    )
}

export default AccordionInfo;