import React, {useState} from "react";
import s from "./inputForm.module.css"

const InputForm = ({label, conditions, maxL, type='text', width, handleProgressElement = false, title}) =>{
    const [value, setValue] = useState('')

    const handleChangeValue = e => {

        if(value.length < maxL || value.length > e.target.value.length ){
            setValue(e.target.value)
        }

    }

    return (
       <div className={s.form} style={width ? {width: `${width}px`} : {}}>
           <div className={s.circle}/>
           <input
              value={value}
              className={s.input}
              type={type}
              maxLength={+maxL}
              placeholder={label}
              required
              onChange={e => {
                  handleChangeValue(e)
                  if(handleProgressElement){
                      return handleProgressElement(title, true)
                  }
              }}
           />
           <p className={s.conditions}>{conditions}</p>
       </div>
   )
}

export default InputForm;