import React from "react";
import InputForm from "../UI/InputForm";
import Checkbox from "../UI/Checkbox";
import s from "./priceItem.module.css";

const PriceItem = ({handleProgressElement}) => {
    return(
        <div className={s.price}>
            <InputForm
                label={'1 000 000 000 ₽'}
                conditions={'Максимум 10 символов'}
                maxL={10}
                type={'number'}
                width={137}
                title={'Цена'}
                handleProgressElement={handleProgressElement}
            />
            <Checkbox text={'Торг'}/>
        </div>
    )
}

export default PriceItem;