import React from "react";
import SelectInput from "./SelectInput";
import s from "./arccardion.module.css"

const Accardion = ({handleProgressElement}) => {
    return (
        <div className={s.arccardion}>
            <SelectInput status={true}  handleProgressElement={handleProgressElement}/>
            <SelectInput />
            <SelectInput />
        </div>
    )
}

export default Accardion