import React from "react";
import s from "./checkbox.module.css";

const Checkbox = ({text}) => {

    const inputIdRandom = Math.random();

    return(
        <div className={s.checkbox}>
            <input className={s.input} id={inputIdRandom} type="checkbox"/>
            <label className={s.label} htmlFor={inputIdRandom}/>
            <span className={s.text}>{text}</span>
        </div>
    )
}

export default Checkbox