import React from "react";
import s from "./progressItem.module.css"

const ProgressItem = ({value, id, status}) => {

    const handleStyleElement = (stat, prop) => {
        // eslint-disable-next-line default-case
        switch (prop){
            case 'bg':
                return stat ? {backgroundColor: '#00A0AB'} : {backgroundColor: '#C7C7C7'};
            case 'fill':
                return  status ? {fill: '#00A0AB'} : {fill: '#C7C7C7'};
            default:
                return stat ? {color: '#00A0AB'} : {color: '#C7C7C7'};
        }
    }

    return(
        <div className={s.progress__item}>
            <div className={s.progress__svg}>
                <a href={`#${value}`} className={s.progress__text} style={handleStyleElement(status)}>{value}</a>
                <svg  width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path style={handleStyleElement(status, 'fill')} d="M16.9999 12.927C13.6561 16.6204 9.5 23 9.5 23C9.5 23 6.55294 18.1087 4.73842 15.5133C2.9239 12.918 0.91325 11.0516 0 10.3407C0 10.3407 0.689806 9.12296 0.80845 8.29892C0.988106 7.05115 0.80845 5.82606 0.80845 5.82606C0.80845 5.82606 3.59311 8.14465 5.21001 9.79626C6.82691 11.4479 9.16244 14.5605 9.16244 14.5605C9.16244 14.5605 13.4553 9.91741 15.8771 7.79981C21.2218 3.12632 24 1 24 1C24 1 23.7754 1.56093 23.198 3.8523C22.695 5.84875 22.6142 7.20996 22.6142 7.20996C22.6142 7.20996 20.6149 8.93416 16.9999 12.927Z"/>
                </svg>
            </div>
            {+id !== 6 ? <div className={s.progress__line} style={handleStyleElement(status, 'bg')}/> : <></>}
        </div>
    )
}

export default ProgressItem;