import React, {useState, useEffect} from "react";
import s from "./photoDownload.module.css"
import PhotoDownloadItem from "./PhotoDownloadItem";

const PhotoDownload = ({handleProgressElement}) => {
    const [selectedFile, setSelectedFile] = useState(undefined);
    const [preview, setPreview] = useState([]);

    //create URL
    useEffect(  () => {

        if (selectedFile === undefined) {
            setPreview([])
            return
        }

        const objectUrl = URL.createObjectURL(selectedFile)
        setPreview(p => p.concat(objectUrl))

        return () => URL.revokeObjectURL(objectUrl)
    }, [selectedFile])

    //loading file user
    const onSelectFile = e => {
        if (!e.target.files || e.target.files.length === 0) {
            setSelectedFile(undefined)
            return
        }
        handleProgressElement('Фотографии', true)
        setSelectedFile(e.target.files[0])
    }

    const deletePhotoItemCard = id => setPreview(preview.filter((el, index) => index !== id))

    return(
        <div action={'#'} className={s.form}>

            <div className={s.circle}/>
            <div className={s.cards}>
                {preview.map(( item, index) => {
                    return <PhotoDownloadItem key={index} src={item} id={index} deletePhotoItemCard={deletePhotoItemCard}/>
                })}
                <input className={s.input} id={'download'} type={'file'}  multiple onChange={onSelectFile} required/>
                <label htmlFor={'download'} className={s.card__download}>
                    <svg className={s.svg} width="54" height="43" viewBox="0 0 54 43" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <mask id="path-1-inside-1" fill="white">
                            <path fillRule="evenodd" clipRule="evenodd" d="M17.4376 0C15.2285 0 13.4376 1.79086 13.4376 4V5.37433H4C1.79086 5.37433 0 7.16519 0 9.37433V38.9997C0 41.2089 1.79086 42.9997 4 42.9997H49.7506C51.9597 42.9997 53.7506 41.2089 53.7506 38.9997V9.37433C53.7506 7.16519 51.9597 5.37433 49.7506 5.37433H40.3129V4C40.3129 1.79086 38.522 0 36.3129 0H17.4376Z"/>
                        </mask>
                        <path d="M13.4376 5.37433V6.37433H14.4376V5.37433H13.4376ZM40.3129 5.37433H39.3129V6.37433H40.3129V5.37433ZM14.4376 4C14.4376 2.34315 15.7808 1 17.4376 1V-1C14.6762 -1 12.4376 1.23858 12.4376 4H14.4376ZM14.4376 5.37433V4H12.4376V5.37433H14.4376ZM4 6.37433H13.4376V4.37433H4V6.37433ZM1 9.37433C1 7.71748 2.34315 6.37433 4 6.37433V4.37433C1.23857 4.37433 -1 6.61291 -1 9.37433H1ZM1 38.9997V9.37433H-1V38.9997H1ZM4 41.9997C2.34314 41.9997 1 40.6566 1 38.9997H-1C-1 41.7612 1.23857 43.9997 4 43.9997V41.9997ZM49.7506 41.9997H4V43.9997H49.7506V41.9997ZM52.7506 38.9997C52.7506 40.6566 51.4074 41.9997 49.7506 41.9997V43.9997C52.512 43.9997 54.7506 41.7612 54.7506 38.9997H52.7506ZM52.7506 9.37433V38.9997H54.7506V9.37433H52.7506ZM49.7506 6.37433C51.4074 6.37433 52.7506 7.71747 52.7506 9.37433H54.7506C54.7506 6.61291 52.512 4.37433 49.7506 4.37433V6.37433ZM40.3129 6.37433H49.7506V4.37433H40.3129V6.37433ZM39.3129 4V5.37433H41.3129V4H39.3129ZM36.3129 1C37.9698 1 39.3129 2.34314 39.3129 4H41.3129C41.3129 1.23858 39.0743 -1 36.3129 -1V1ZM17.4376 1H36.3129V-1H17.4376V1Z" fill="#C7C7C7" mask="url(#path-1-inside-1)"/>
                        <circle  cx="26.8753" cy="24.1875" r="12.9376" stroke="#C7C7C7"/>
                        <circle r="2.18753" transform="matrix(-1 0 0 1 45.688 13.4374)" stroke="#C7C7C7"/>
                    </svg>
                    <span className={s.text}>Добавьте или перетащите фото</span>
                </label>
            </div>
        </div>
    )
}

export default PhotoDownload;