import {MenuItem, Select} from "@material-ui/core";
import s from "./selectInput.module.css";
import React, {useState} from "react";

const SelectInput = ({status, handleProgressElement}) =>{

    const [value, setValue] = useState('выбор')

    const handleChangeSelect = e =>setValue(e.target.value)

    const arrow = <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M2.11707 8.954L9.07108 16.1421L16.0251 8.954" stroke="#C7C7C7" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
    </svg>

    return(
        <div className={s.form}>
            <div className={s.circle} style={status ? {display : 'block'} : {display: 'none'}}/>
            <Select
                className={s.select}
                value={value}
                onChange={ e => {
                    handleChangeSelect(e)
                    if(status) {
                        if(e.target.value !== 'выбор') {
                            return handleProgressElement( 'Категория', true )
                        }
                        return handleProgressElement( 'Категория', false )
                    }
                }}
                style={value !== 'выбор' ? {color: '#5A5A5A'} : {color: '#C7C7C7'}}
                disableUnderline
                required={status}
                IconComponent={() =>arrow}
            >
                <MenuItem value={'выбор'} style={{color:'#C7C7C7'}}>Выберете категорию</MenuItem>
                <MenuItem value={'Машина'}>Машина</MenuItem>
                <MenuItem value={'Телефон'}>Телефон</MenuItem>
                <MenuItem value={'одежда'}>Одежда</MenuItem>
            </Select>
        </div>
    )
}

export default SelectInput;